.body {
  box-sizing: border-box;
  margin: 0;
  min-width: 250px;
  width: 100vw;
  /* (чтоб на ширину не влиял скролл) ширина 100% экрана */
  min-height: 100vh;
  overflow-x: hidden;
  display: grid;
  justify-items: center;
  grid-template-rows: 0fr 1fr 0fr;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
  grid-auto-columns: auto;
  grid-template-areas: "grid_header" "grid_main" "grid_footer";
}
.body::-webkit-scrollbar {
  width: 10px;
  /* ширина scrollbar */
  height: 2px;
  transform: translateY(0px);
}
.body::-webkit-scrollbar-track {
  background-color: rgba(122, 122, 122, 0.5);
  /* цвет дорожки */
}
.body::-webkit-scrollbar-thumb {
  background: #464646;
  /* цвет плашки */
  border: none;
  /* padding вокруг плашки */
}
.body--dark {
  background-color: #232529;
}
.body--dark::-webkit-scrollbar {
  width: 10px;
  /* ширина scrollbar */
  height: 2px;
  transform: translateY(0px);
}
.body--dark::-webkit-scrollbar-track {
  background: #ffffff17;
  /* цвет дорожки */
}
.body--dark::-webkit-scrollbar-thumb {
  background-color: #ffffff;
  border: none;
  /* padding вокруг плашки */
}
.body__header {
  grid-area: grid_header;
  margin-top: 10px;
  align-self: start;
  width: 95%;
  max-width: 1000px;
}
